.modal {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: hsla(0, 0%, 63%, 0.5);
  overflow: auto;
}
.modal p {
  line-height: 1.5;
}
.modal__sponsor-banner {
  position: fixed;
  z-index: 1;
  width: 100%;
  color: #dd2000;
  background-color: hsla(9, 100%, 86%, 0.925);
  font-size: 0.9em;
  line-height: 1.33;
  text-align: center;
  padding: 0.25em 1em;
}
.modal__inner-1 {
  margin: 0 auto;
  width: 100%;
  min-width: 320px;
  max-width: 480px;
}
.modal__inner-2 {
  margin: 40px 10px 100px;
  background-color: #f8f8f8;
  padding: 50px 50px 40px;
  border-radius: 3px;
  position: relative;
  overflow: hidden;
}
.modal__inner-2:before {
  top: 0;
  background-image: linear-gradient(
    270deg,
    gold,
    gold 23%,
    #a5c700 27%,
    #a5c700 48%,
    #ff8a00 52%,
    #ff8a00 73%,
    #66aefd 77%
  );
}
.modal__inner-2:after,
.modal__inner-2:before {
  content: '';
  position: absolute;
  left: 0;
  height: 3px;
  width: 100%;
}
.modal__inner-2:after {
  bottom: 0;
  background-image: linear-gradient(
    90deg,
    gold,
    gold 23%,
    #a5c700 27%,
    #a5c700 48%,
    #ff8a00 52%,
    #ff8a00 73%,
    #66aefd 77%
  );
}
.modal__content > .modal__image:first-child + *,
.modal__content > :first-child {
  margin-top: 0;
}
.modal__image {
  float: left;
  width: 60px;
  height: 60px;
  margin: 1.5em 1.2em 0.5em 0;
}
.modal__image + :after {
  content: '';
  display: block;
  clear: both;
}
.modal__title {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.4;
  margin-top: 2.5rem;
}
.modal__sub-title {
  opacity: 0.6;
  font-size: 0.75rem;
  margin-bottom: 1.5rem;
}
.modal__error {
  color: #de2c00;
}
.modal__info {
  background-color: rgba(255, 150, 50, 0.15);
  border-radius: 3px;
  margin: 1.2em 0;
  padding: 0.75em 1.25em;
  font-size: 0.95em;
  line-height: 1.6;
}
.modal__info pre {
  line-height: 1.5;
}
.modal__info--multiline {
  padding-top: 0.1em;
  padding-bottom: 0.1em;
}
.modal__button-bar {
  margin-top: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.form-entry {
  margin: 1em 0;
}
.form-entry__label {
  display: block;
  font-size: 0.9rem;
  color: gray;
}
.form-entry--focused .form-entry__label {
  color: #0974b4;
}
.form-entry--error .form-entry__label {
  color: #dd2000;
}
.form-entry__label-info {
  font-size: 0.75rem;
}
.form-entry__field {
  border: 1px solid #b0b0b0;
  border-radius: 3px;
  position: relative;
  overflow: hidden;
}
.form-entry--focused .form-entry__field {
  border-color: #0c93e4;
  box-shadow: 0 0 0 2.5px rgba(12, 147, 228, 0.33);
}
.form-entry--error .form-entry__field {
  border-color: #f31;
  box-shadow: 0 0 0 2.5px rgba(255, 51, 17, 0.33);
}
.form-entry__actions {
  text-align: right;
  margin: 0.25em;
}
.form-entry__button {
  width: 38px;
  height: 38px;
  padding: 6px;
  display: inline-block;
  background-color: transparent;
  opacity: 0.75;
}
.form-entry__button:active,
.form-entry__button:focus,
.form-entry__button:hover {
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.1);
}
.form-entry__checkbox,
.form-entry__radio {
  margin: 0.25em 1em;
}
.form-entry__checkbox input,
.form-entry__radio input {
  margin-right: 0.25em;
}
.form-entry__info {
  font-size: 0.75em;
  opacity: 0.67;
  line-height: 1.4;
  margin: 0.25em 0;
}
.tabs {
  border-bottom: 1px solid hsla(0, 0%, 50%, 0.33);
  margin: 1em 0 2em;
}
.tabs:after {
  content: '';
  display: block;
  clear: both;
}
.tabs__tab {
  width: 50%;
  float: left;
  text-align: center;
  line-height: 1.4;
  font-weight: 400;
  font-size: 1.1em;
}
.tabs__tab > a {
  width: 100%;
  text-decoration: none;
  padding: 0.67em 0.33em;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  color: #0c93e4;
}
.tabs__tab > a:focus,
.tabs__tab > a:hover {
  background-color: rgba(0, 0, 0, 0.05);
}
.tabs__tab--active > a {
  border-bottom: 2px solid #0c93e4;
  color: inherit;
}
.notification {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  max-width: 340px;
}
.notification__item {
  margin: 10px;
  padding: 10px 15px;
  line-height: 1.4;
  background-color: #000;
  color: #fff;
  font-size: 0.9em;
  border-radius: 3px;
}
.notification__icon {
  height: 20px;
  width: 20px;
  margin-right: 12px;
  flex: none;
}
.notification__button {
  color: #b9b9b9;
  padding: 8px;
  flex: none;
}
.notification__button:active,
.notification__button:focus,
.notification__button:hover {
  color: #fff;
  background-color: hsla(0, 0%, 100%, 0.1);
}
.context-menu {
  position: absolute;
  width: 100%;
  height: 100%;
  font-size: 14px;
  line-height: 18px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial,
    sans-serif;
  user-select: none;
}
.context-menu__inner {
  position: absolute;
  background-color: #ebebeb;
  border-radius: 5px;
  padding: 5px 0;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.16), 0 3px 10px 1px rgba(0, 0, 0, 0.12);
}
.context-menu__item {
  display: block;
  color: #333;
  text-decoration: none;
  padding: 0 25px;
}
a.context-menu__item:active,
a.context-menu__item:focus,
a.context-menu__item:hover {
  background-color: #338dfc;
  color: #fff;
}
.context-menu__item--disabled {
  color: #aaa;
}
.context-menu__separator {
  border-top: 2px solid #dcdcdd;
  margin: 5px 0;
}
.splash-screen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 25px;
}
.splash-screen__inner {
  margin: 0 auto;
  max-width: 600px;
  height: 100%;
}
